/* Estilos generales */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #00D2D3, #009E9E); /* Aqua y verde suave */
  color: #fff;
  text-align: center;
  overflow: hidden;
  animation: bodyBackground 8s ease-in-out infinite alternate;
}

/* Animación del fondo */
@keyframes bodyBackground {
  0% { background: linear-gradient(135deg, #00D2D3, #009E9E); }
  25% { background: linear-gradient(135deg, #ff6a00, #ffbb33); /* Naranja cálido */ }
  50% { background: linear-gradient(135deg, #ff61a6, #d80069); /* Rosa y fucsia */ }
  75% { background: linear-gradient(135deg, #32CD32, #006400); /* Verde brillante */ }
  100% { background: linear-gradient(135deg, #00BFFF, #0072FF); /* Azul eléctrico */ }
}

/* Animación de desvanecimiento (Fade In) */
@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(-20px); }
  100% { opacity: 1; transform: translateY(0); }
}

/* Animación de escala */
@keyframes scaleUp {
  0% { transform: scale(1); }
  100% { transform: scale(1.1); }
}

/* Animación de deslizamiento desde la derecha */
@keyframes slideIn {
  0% { transform: translateX(100%); opacity: 0; }
  100% { transform: translateX(0); opacity: 1; }
}

/* Animación de rebote (Bounce) */
@keyframes bounce {
  0% { transform: translateY(0); }
  20% { transform: translateY(-10px); }
  40% { transform: translateY(0); }
  60% { transform: translateY(-5px); }
  100% { transform: translateY(0); }
}

/* Animación de "blink" (parpadeo) */
@keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 1; }
}

/* Contenedor principal */
.container {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 40px;
  width: 100%;
  max-width: 900px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  animation: containerIn 1.2s ease-out;
}

@keyframes containerIn {
  0% { opacity: 0; transform: translateY(50px); }
  100% { opacity: 1; transform: translateY(0); }
}

/* Animación de entrada de texto */
h1 {
  font-size: 50px;
  color: #fff;
  margin-bottom: 40px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 7px;
  text-shadow: 3px 3px 15px rgba(0, 0, 0, 0.5);
  animation: fadeIn 2s ease-out;
}

/* Input de tarea */
input[type='text'] {
  width: 75%;
  padding: 15px;
  border: 2px solid #fff;
  border-radius: 25px;
  margin-right: 15px;
  font-size: 20px;
  background-color: rgba(255, 255, 255, 0.2);
  color: #00D2D3; /* Aqua */
  transition: all 0.4s ease-in-out;
  animation: inputGlow 2s ease-in-out infinite alternate;
}

@keyframes inputGlow {
  0% { box-shadow: 0 0 10px rgba(0, 255, 255, 0.8); }
  100% { box-shadow: 0 0 15px rgba(50, 205, 50, 0.8); }
}

input[type='text']:focus {
  border-color: #32C7B7; /* Verde menta */
  outline: none;
  box-shadow: 0 0 15px rgba(50, 205, 50, 0.8);
  transform: scale(1.1);
  color: #fff;
}

/* Botones */
button {
  background: linear-gradient(135deg, #FF8C00, #FF5E00); /* Naranja cálido y oscuro */
  color: white;
  padding: 18px 35px;
  border: 2px solid #FF8C00;
  border-radius: 30px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  transition: all 0.3s ease-out;
  box-shadow: 0 6px 18px rgba(255, 140, 0, 0.4);
  animation: buttonPulse 1.5s ease-in-out infinite alternate;
}

@keyframes buttonPulse {
  0% { transform: scale(1); box-shadow: 0 6px 18px rgba(255, 140, 0, 0.4); }
  100% { transform: scale(1.1); box-shadow: 0 8px 25px rgba(255, 140, 0, 0.7); }
}

button:hover {
  background: linear-gradient(135deg, #FF6600, #FF4D00); /* Naranja más oscuro */
  transform: translateY(-5px);
  box-shadow: 0 0 30px rgba(255, 102, 0, 0.8);
}

button.delete {
  background: linear-gradient(135deg, #FF6A00, #FF3333); /* Rosa cálido y naranja */
}

button.delete:hover {
  background: linear-gradient(135deg, #FF4D4D, #D32F2F);
  transform: translateY(-5px);
  box-shadow: 0 0 30px rgba(255, 0, 0, 0.8);
}

button.update {
  background: linear-gradient(135deg, #FFB600, #FF8C00); /* Amarillo brillante y naranja */
}

button.update:hover {
  background-color: #FFB86C;
  transform: translateY(-5px);
  box-shadow: 0 0 30px rgba(255, 165, 0, 0.8);
}

/* Lista de tareas */
ul {
  list-style-type: none;
  padding: 0;
  margin-top: 40px;
}

li {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(5px);
  transition: transform 0.3s ease, background-color 0.3s ease;
}

li:hover {
  transform: translateY(-5px);
  background-color: rgba(255, 255, 255, 0.15);
}

li button {
  margin-left: 15px;
  font-size: 16px;
  transition: transform 0.3s ease;
}

li button.delete {
  background-color: #FF5E62; /* Rosa cálido */
}

li button.delete:hover {
  background-color: #D32F2F;
  transform: scale(1.1);
}

/* Efectos de transición */
input[type="text"]:focus, button:hover {
  transform: scale(1.1);
}

li button:hover {
  transform: scale(1.05);
}
